<template>
  <div class="notice">
    <header-view :headerObj="{title:'工作通知',count:0}"></header-view>
    <ul>
      <li v-for="item in noticeMsg">
        <p><span>{{item.date}}</span></p>
        <div class="imgDiv" @click="jump(item.appUrl)">
          <img :src="item.imgSrc" alt="">
        </div>
        <div class="content" @click="jump(item.contentUrl)">
          <p>{{item.title}}</p>
          <div :style="{borderTop:'3px solid '+item.themeColor}">
            <p :style="{color:item.themeColor}">{{item.title}}</p>
            <div>{{item.content}}</div>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
  import HeaderView from '../../components/appStore/header-x'
  export default {
    name:"notice",
    data(){
      return {
        noticeMsg:[{
          imgSrc:"../../../static/img/aike.png",
          title:"爱克进销存",
          themeColor:"#16C295",
          date:"6月22号",
          appUrl:"/appStore",
          contentUrl:"/dingVersion",
          content:"您当前使用的爱克进销存体验VIP版已经到期！为了不影响您的正常使用，请及时联系我们进行升级，谢谢！"+
                  "<br/>联系电话：400-867-1101"
          },{
            imgSrc:"../../../static/img/hotRecomm.png",
            title:"小圈ERP",
            date:"7月23号",
            themeColor:"#F8B45E",
            appUrl:"/appStore",
            contentUrl:"/dingVersion",
            content:"恭喜你团队已开通【小圈ERP】，专注中小企业的库存管理，使用它有助于帮您解决库存积压和不足等问题，"+
                    "让库存管理更精准！软件自带BOM(物料清单),自动计算"
        }]
      }
    },
    components:{HeaderView},
    methods:{
      jump(url){
        this.$router.push({path:url})
      }
    }
  }
</script>
<style lang="less" scoped>
  .notice{
    padding-top: 2rem;
    background: #eee;
    ul{
      >li{
        padding:.5rem;
        >p{
          margin: .5rem 0;
          text-align: center;
          >span{
            color:#fff;
            background: #ccc;
            padding:.25rem;
            border-radius: 7px;
            font-size: 12px;
          }
        }
        >div{
          display: inline-block;
        }
        .imgDiv{
          >img{width: 64px;}
          // width: 64px;
          // height: 64px;
          border-radius: 50%;
          overflow: hidden;
          width: 10%;
          margin-right: .5rem;
          vertical-align: top;
        }
        .content{
          width: 80%;
          >p{color:#666;padding:.25rem 0;}
          >div{
            background: #fff;
            padding:.5rem;
            >p{
              padding:.25rem 0;
            }
            >div{
              padding:.25rem 0 .5rem 0;
              line-height: 1rem;
              color:#333;
            }
          }
        }
      }
    }
  }
</style>



